import { h, Component } from "preact"
import Image from 'pimg/preact'

export class Galleries extends Component {

  constructor() {
    super();
    this.state = {
      galleries: []
    };
  }

  // Lifecycle: Called whenever our component is created
  componentDidMount() {
    const galleries = []
    const random = (min, max) => {
      return Math.round(Math.random() * (max - min) + min)
    }
    const cdnImage = (uri) => {
      return `https://images1-focus-opensocial.googleusercontent.com/gadgets/proxy?url=${encodeURIComponent(uri)}&container=focus`
    }

    for (let index = 0; index < 25; index++) {
      const size = { width: random(200, 400), height: random(200, 400) }
      galleries.push({
        url: cdnImage(`https://placekitten.com/${size.width}/${size.height}`),
        ...size
      })
    }

    this.setState({ galleries })
  }

  render() {
    const renderedDOM = this.state.galleries.map((gallery, index) => (
        <Image src={gallery.url} />
    ))

    return <div class='galleries-wrap'>{renderedDOM}</div>
  }
}